import {Component} from '@angular/core';
import {NavController, NavParams, ViewController} from 'ionic-angular';
import {ContactProvider} from "../../providers/contact/contact";

@Component({
    selector: 'page-store-filter',
    templateUrl: 'store-filter.html',
})
export class StoreFilterPage {

    public tags:any;
    public categories:any;
    public tagFilter:any = [];
    public categoryFilter:any = [];
    public visitFilter:any = [];

    constructor(public navCtrl: NavController,
                private viewCtrl: ViewController,
                private _contactProvider: ContactProvider,
                public navParams: NavParams) {
        console.log(this.navParams.data);
        if (this.navParams.data != false) {
            this.tagFilter = this.navParams.data.tagFilter;
            this.categoryFilter = this.navParams.data.categoryFilter;
            // this.visitFilter = this.navParams.data.visitFilter;
        }
    }

    public back() {
        this.viewCtrl.dismiss();
    }

    public apply() {
        this.viewCtrl.dismiss({
            tagFilter: this.tagFilter,
            // visitFilter: this.visitFilter,
            categoryFilter: this.categoryFilter
        })
    }

    ionViewWillEnter() {
        this._contactProvider.getTags()
            .subscribe(tags => {
                this.tags = tags;
            });

        this._contactProvider.getCategories()
            .subscribe(categories => {
                this.categories = categories;
            });
    }

}



