import {Component, ViewChild} from '@angular/core';
import {Content, NavController, NavParams, ViewController} from 'ionic-angular';


@Component({
    selector: 'page-order-cart-edit',
    templateUrl: 'order-cart-edit.html',
})
export class OrderCartEditPage {
    @ViewChild(Content) content: Content;

    // TODO what to give to model cart
    // TODO model cart add data
    // TODO represetn in calc
    // TODO present in article
    // TODO on edit inside cart

    public price: any;
    public newPrice: any;
    public consumerPrice: any;
    public marg_pr: any;
    public marg_kr: any;
    public discount: any;
    public quantity: any;
    public tax: any;
    public article: any;


    constructor(public navCtrl: NavController,
                public viewCtrl: ViewController,
                public navParams: NavParams) {

    }

    ionViewWillEnter() {
        this.article = this.navParams.data.item;

        this.price = parseFloat(this.article.price);
        this.quantity = parseInt(this.article.quantity);
        this.tax = parseFloat(this.article.tax);
        this.newPrice = parseFloat(this.article.newPrice);
        this.consumerPrice = parseFloat(this.article.consumerPrice);
        this.discount = 0;

        this.changePrice();
        this.changeConsumerPrice();
        this.content.resize();

    }

    public changePrice() {
        setTimeout(() => {
            this.discount = (this.price - this.newPrice) * 100 / this.price;
        }, 100)

    }

    public changeNewPrice() {
        setTimeout(() => {
            this.discount = (this.price - this.newPrice) * 100 / this.price;

            this.marg_kr = (this.consumerPrice * 1 / (1 + this.tax / 100)) - this.newPrice;

            this.marg_pr = (((this.consumerPrice * 1 / (1 + this.tax / 100))
                - this.newPrice) / (this.consumerPrice * 1 / (1 + this.tax / 100))) * 100;

        }, 100)
    }

    public changeConsumerPrice() {
        setTimeout(() => {
            this.marg_kr = (this.consumerPrice * 1 / (1 + this.tax / 100)) - this.newPrice;

            this.marg_pr = (((this.consumerPrice * 1 / (1 + this.tax / 100))
                - this.newPrice) / (this.consumerPrice * 1 / (1 + this.tax / 100))) * 100;

        }, 100)
    }

    public changeMargPr() {
        setTimeout(() => {
            let value = this.newPrice / (1 - this.marg_pr / 100);
            // plus tax
            value = value * (1 + this.tax / 100);
            this.consumerPrice = (value * 100) / 100;

            this.marg_kr = (this.consumerPrice * 1 / (1 + this.tax / 100)) - this.newPrice;

        }, 100)
    }

    public changeMargKr() {
        setTimeout(() => {
            let value = this.newPrice + this.marg_kr;
            value = value * (1 + this.tax / 100);
            this.consumerPrice = (value * 100) / 100;

            this.marg_pr = (((this.consumerPrice * 1 / (1 + this.tax / 100))
                - this.newPrice) / (this.consumerPrice * 1 / (1 + this.tax / 100))) * 100;
        })
    }

    // public changeDiscount() {
    //     setTimeout(() => {
    //         let value = this.newPrice + this.marg_kr;
    //         value = value * (1 + this.tax / 100);
    //         this.consumerPrice = (value * 100) / 100;
    //
    //         this.marg_pr = (((this.consumerPrice * 1 / (1 + this.tax / 100))
    //             - this.newPrice) / (this.consumerPrice * 1 / (1 + this.tax / 100))) * 100;
    //     })
    // }

    public back() {
        this.viewCtrl.dismiss(false);
    }

    public save() {
        this.viewCtrl.dismiss({
            customPrice: this.price,
            newPrice: this.newPrice,
            consumerPrice: this.consumerPrice,
            marg_pr: this.marg_pr,
            marg_kr: this.marg_kr,
            discount: this.discount,
            quantity: this.quantity,
            tax: this.tax,
        });
    }

}




