import { Storage } from '@ionic/storage';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';

@Injectable()
export class AuthService {
    public token: string;
    public accesses: any;
    public seller: any;

    constructor(private storage: Storage) {
        this.storage.get('user').then(currentUser => {
            if (currentUser) {
                this.init(currentUser);
            }
        });
    }

    init(user: any) {
        this.token = user.auth_token;
        this.seller = user.seller;
        this.accesses = user.accesses;
    }

    getToken() {
        let promise = this.storage.get('user').then(currentUser => {
            this.token = currentUser && currentUser.auth_token;
            return this.token;
        });
        return Observable.fromPromise(promise);
    }

    getSeller() {
        return this.seller;
    }
    getAccesses() {
        return this.accesses;
    }
}


