import { HttpClient } from '@angular/common/http';
import { SalesPeriod } from './../../app/models/sales-period';
import { CommonService } from './../../providers/common.service';
import { Component } from '@angular/core';
import { App, LoadingController, NavController, NavParams, ModalController, AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { FormPage } from "../form/form";
import * as moment from 'moment';
import { DistributionPage } from "../distribution/distribution";
import { OrderCartEditPage } from "../order-cart-edit/order-cart-edit";
import { HttpParams } from '@angular/common/http';
import { Globals } from '../../providers/globals';

/**
 * Generated class for the ActionsPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */


@Component({
    selector: 'page-store-details-actions',
    templateUrl: 'store-details-actions.html',
})
export class StoreDetailsActionsPage {

    public store: any;
    public salesPeriod: number;
    public salesPeriods: SalesPeriod[];
    public group_actions: any = {};
    public ungrouped_actions: any = [];
    public groups: any = [];
    public distributions: any = [];
    public actions_loader;
    public outcomes: any = [];

    constructor(public navCtrl: NavController,
        public navParams: NavParams,
        public loader: LoadingController,
        private app: App,
        public modalCtrl: ModalController,
        private alertCtrl: AlertController,
        public http: HttpClient,
        private commonService: CommonService) {
        this.store = this.navParams.data;
    }

    ionViewWillEnter() {
        this.actions_loader = this.loader.create({
            content: 'Loading data'
        });
        this.actions_loader.present().then(() => {
            this.commonService.fetchSalesPeriods().subscribe(salesPeriods => {
                this.salesPeriods = salesPeriods;
                this.setCurrentSalesPeriod();
                this.downloadActions(this.salesPeriod);
                this.downloadDistributions(this.salesPeriod);           
            });
        });
    }

    onSalesPeriodChanged() {
        this.downloadActions(this.salesPeriod);
        this.downloadDistributions(this.salesPeriod);
    }

    showDistribution(distribution) {
        setTimeout(() => {

            this.navCtrl.push(DistributionPage, { distribution: distribution, store: this.store })
        }, 200);

    }

    showAction(action) {
        let modal = this.modalCtrl.create(FormPage, { action: action, store: this.store });
        modal.present();
    }

    downloadDistributions(salesPeriodId = undefined) {
        // let actions_loader = this.loader.create({
        //     content: 'Loading distributions'
        // });
        let params = new HttpParams()
            .set('salesPeriod', salesPeriodId)
            .set('storeId', this.store.id);
        // actions_loader.present().then(() => {
        this.http.get(Globals.api + 'app/distribution', { params: params }).subscribe((distributions: any) => {
            this.distributions = distributions;
            let params = new HttpParams().append('storeId', this.store.id);
            this.http.get(Globals.api + 'app/distribution/listByOutcome', {params}).subscribe((distscore: any) => {

                for (let key in distscore.outcomes) {
                    for (let bla in distscore.distributions) {
                        if (distscore.distributions[bla].id == key) {
                            this.outcomes[key] = parseInt(distscore.outcomes[key].sold) / parseInt(distscore.outcomes[key].articles_count);
                        }
                    }
                }
            });
            this.actions_loader.dismissAll();
        })
        // });
    }

    downloadActions(salesPeriodId) {
        // let actions_loader = this.loader.create({
        //     content: 'Loading actions'
        // });
        // actions_loader.present().then(() => {
            let params = new HttpParams().append('storeId', this.store.id);
            this.http.get(Globals.api + 'app/actions/groupList', {params}).subscribe((groups: any) => {
                this.groups = [];
                for (let group of groups) {
                    group.id = parseInt(group.id);
                    group.show = false;
                    this.groups.push(group);
                }
                let params = new HttpParams()
                    .set('salesPeriod', salesPeriodId)
                    .set('storeId', this.store.id)
                    .set('expand', 'formSettings');



                this.http.get(Globals.api + 'app/actions', { params: params }).subscribe((actions: any) => {
                    this.group_actions = {};
                    this.ungrouped_actions = [];

                    for (let action of actions) {
                        if (!this.group_actions[action.form_group_id]) {
                            this.group_actions[action.form_group_id] = [];
                        }
                        if (!action.form_group_id) this.ungrouped_actions.push(action);
                        this.group_actions[action.form_group_id].push(action);
                    }
                    // actions_loader.dismissAll();
                });


            });
        // });
    }

    setCurrentSalesPeriod() {
        let nowMoment = moment();
        let currentSalesPeriod = this.salesPeriods.find(s => {
            return nowMoment.isBetween(s.startDate, s.endDate, 'day', '[]');
        });
        if (currentSalesPeriod) {
            this.salesPeriod = currentSalesPeriod.id;
        }
    }

    public back() {
        this.app.navPop();
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad ActionsPage');
    }


}



