import {Component, ViewChild} from '@angular/core';
import {
    AlertController, Content, Events, ItemSliding, LoadingController, ModalController, NavController, NavParams,
    ViewController
} from 'ionic-angular';
import {OrderProvider} from "../../providers/order/order";
import {OrderFormationAddOrderPage} from "../order-formation-add-order/order-formation-add-order";
import * as moment from 'moment';
import {OrderCartPage} from "../order-cart/order-cart";
import {OrderCalculatorPage} from "../order-calculator/order-calculator";


@Component({
    selector: 'page-order-formation',
    templateUrl: 'order-formation.html',
})
export class OrderFormationPage {
    @ViewChild(Content) content: Content;

    public article_group: any;
    public articles: any = [];
    public articles_super: any = [];
    public order_types: any;
    public listings: any;
    public pricing: any;
    public store: any;
    public listing_filter: any;
    public cart: any = {
        orders: [],
        shareEmails: ""
    };
    public order: any = false;
    public new_order: any;


    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                private loader: LoadingController,
                private modalCtrl: ModalController,
                private alertCtrl: AlertController,
                private viewCtrl: ViewController,
                public events: Events,
                private _orderProvider: OrderProvider) {
    }

    ionViewWillEnter() {

        this.articles = [];
        this.articles_super = [];

        if (this.navParams.data != false) {
            this.article_group = this.navParams.data.article_group;
            this.store = this.navParams.data.store;
            this.cart = this.navParams.data.cart;

            this.navParams.data = false;
        }

        let loader = this.loader.create({
            content: 'Loading articles'
        });

        loader.present().then(() => {
            this._orderProvider.getArticles({expand: 'group,articleUnitJoin'})
                .subscribe(articles => {
                    this._orderProvider.getListings({store_id: this.store.id})
                        .subscribe(listings => {
                            this.listings = listings;
                            for (let art_key in articles) {
                                let checker = false;
                                for (let art_id in listings) {
                                    if (art_id == articles[art_key].id) {
                                        let bla_bla = {
                                            brand: articles[art_key].brand_id,
                                            suppArtNr: articles[art_key].suppArtNr,
                                            tax: articles[art_key].tax,
                                            group: articles[art_key].group,
                                            img66: articles[art_key].img66,
                                            id: articles[art_key].id,
                                            product: articles[art_key].product,
                                            price: articles[art_key].price,
                                            listing: listings[art_id],
                                            quantity: 0
                                        };
                                        this.articles.push(bla_bla);
                                        this.articles_super.push(bla_bla);
                                        checker = true;
                                        break;
                                    }
                                }

                                if (checker == false) {
                                    let ah_ah = {
                                        brand: articles[art_key].brand_id,
                                        suppArtNr: articles[art_key].suppArtNr,
                                        tax: articles[art_key].tax,
                                        group: articles[art_key].group,
                                        img66: articles[art_key].img66,
                                        id: articles[art_key].id,
                                        product: articles[art_key].product,
                                        price: articles[art_key].price,
                                        listing: " ",
                                        quantity: 0
                                    };
                                    this.articles.push(ah_ah);
                                    this.articles_super.push(ah_ah);
                                }
                            }

                            if (this.article_group != null) {
                                let ar_id = this.article_group.id;
                                this.articles = this.articles.filter(function (article) {
                                    return article.group.id === ar_id;
                                });
                                this.articles_super = this.articles_super.filter(function (article) {
                                    return article.group.id === ar_id;
                                });
                            }

                            if (this.cart.orders.length > 0) {
                                for (let some_key in this.cart.orders[this.cart.orders.length - 1].orderItems) {
                                    for (let an_key in this.articles) {
                                        if (this.articles[an_key].id == this.cart.orders[this.cart.orders.length - 1].orderItems[some_key].articleId) {

                                            this.articles[an_key].quantity = this.cart.orders[this.cart.orders.length - 1].orderItems[some_key].quantity;
                                            // this.articles_super[an_key].quantity = this.order.orderItems[some_key].quantity;

                                        }
                                    }
                                }
                                this.content.resize();
                            }
                            this.content.resize();
                            loader.dismissAll();
                        });
                });
        });

        // TODO cart click problem, reset do, calculator, add order pushing

        this._orderProvider.getOrderTypes({indexBy: 'id'})
            .subscribe(orderTypes => {
                this.order_types = orderTypes;
            });

        this._orderProvider.getPricing({store_id: this.store.id})
            .subscribe(pricing => {
                this.pricing = pricing;
            });

    }

    public listingFilter() {
        this.articles = this.articles_super;
        let list = [];
        for (let inpr in this.listings) {
            list.push(this.listings[inpr]);
        }
        list = list.filter((x, i, a) => a.indexOf(x) == i);

        let alert = this.alertCtrl.create();
        alert.setTitle('Listing Filter');

        alert.addInput({
            type: 'radio',
            label: 'All Articles',
            value: 'all',
        });
        for (let key in list) {
            alert.addInput({
                type: 'radio',
                label: list[key],
                value: list[key],
            });
        }

        alert.addButton('Cancel');
        alert.addButton({
            text: 'Ok',
            handler: (data: any) => {
                if (data == 'all') {
                    this.articles = this.articles_super;
                } else {
                    this.articles = this.articles.filter(function (article) {
                        return article.listing === data;
                    });
                }
            }
        });
        alert.present();
    }

    public addOrder() {
        let modal = this.modalCtrl.create(OrderFormationAddOrderPage, {order_types: this.order_types});
        modal.onDidDismiss(data => {
            if (data != null) {
                data.deliveryDate = moment(data.deliveryDate).format('YYYY-MM-DD');

                this.order = data;
                for (let key in this.order_types) {
                    if (this.order_types[key].id == this.order.orderTypeId) {
                        this.new_order = this.order_types[key];
                    }
                }

                this.content.resize();
                this._orderProvider.getPricing({store_id: this.store.id, delivery_date: this.order.deliveryDate})
                    .subscribe(pricing => {
                        for (let blakey in this.articles) {
                            for (let article_id in pricing) {
                                if (article_id == this.articles[blakey].id) {
                                    this.articles[blakey].price = pricing[article_id];
                                }
                            }
                        }
                    });


                let duplicate_order = this.cart.orders.findIndex(
                    order => order.deliveryDate == data.deliveryDate && order.orderTypeId == data.orderTypeId);


                if (duplicate_order == -1) {
                    this.cart.orders.push(this.order);
                    for (let index_ar in this.articles) {
                        this.articles[index_ar].quantity = 0;
                        // this.articles_super[index_ar].quantity = 0;
                    }
                } else {
                    this.cart.orders.push(this.cart.orders.splice(duplicate_order, 1)[0]);
                    for (let index_ar in this.articles) {
                        this.articles[index_ar].quantity = 0;
                    }

                    if (this.cart.orders[this.cart.orders.length - 1].orderItems.length > 0) {
                        for (let index_ar in this.articles) {
                            for (let index_oitems in this.cart.orders[this.cart.orders.length - 1].orderItems) {
                                if (this.articles[index_ar].id == this.cart.orders[this.cart.orders.length - 1].orderItems[index_oitems].articleId) {
                                    this.articles[index_ar].quantity = this.cart.orders[this.cart.orders.length - 1].orderItems[index_oitems].quantity;
                                }
                            }
                        }
                    }
                }
            }
        });
        modal.present();
    }

    public addArticle(article) {
        if (this.cart.orders.length == 0) {
            let alert = this.alertCtrl.create({
                title: 'Cart is empty',
                subTitle: 'To create a new order please go to the "+" button',
                buttons: ['Got it!']
            });
            alert.present();
        } else {







            let foundIndex = this.articles.findIndex(art => art.id == article.id);
            this.articles[foundIndex].quantity += 1;
            // this.articles_super[foundIndex].quantity += 1;

            let orderItemIndex = this.cart.orders[this.cart.orders.length - 1].orderItems.findIndex(item => item.articleId == this.articles[foundIndex].id);
            if (orderItemIndex == -1) {
                this.cart.orders[this.cart.orders.length - 1].orderItems.push({
                    brand: this.articles[foundIndex].brand,
                    suppArtNr: this.articles[foundIndex].suppArtNr,
                    tax: this.articles[foundIndex].tax,
                    img66: this.articles[foundIndex].img66,
                    product: this.articles[foundIndex].product,
                    listing: this.articles[foundIndex].listing,
                    articleId: this.articles[foundIndex].id,
                    consumerPrice: 0,
                    customPrice: null,
                    discountPercent: 0,
                    price: this.articles[foundIndex].price,
                    quantity: this.articles[foundIndex].quantity,
                    newPrice: 0,
                    marg_pr: 0,
                    marg_kr: 0,
                });
            } else {
                this.cart.orders[this.cart.orders.length - 1].orderItems[orderItemIndex] = {
                    brand: this.articles[foundIndex].brand,
                    suppArtNr: this.articles[foundIndex].suppArtNr,
                    tax: this.articles[foundIndex].tax,
                    img66: this.articles[foundIndex].img66,
                    product: this.articles[foundIndex].product,
                    listing: this.articles[foundIndex].listing,
                    articleId: this.articles[foundIndex].id,
                    consumerPrice: 0,
                    customPrice: null,
                    discountPercent: 0,
                    price: this.articles[foundIndex].price,
                    quantity: this.articles[foundIndex].quantity,
                    newPrice: 0,
                    marg_pr: 0,
                    marg_kr: 0,
                };
            }
        }
    }

    public goToCart() {
        if (this.cart.orders.length > 0) {
            let modal = this.modalCtrl.create(OrderCartPage, {cart: this.cart, store: this.store});
            modal.onDidDismiss(data => {
                this.cart = data;
            });
            modal.present();
        }
    }

    public reset(item_index, article, slidingItem: ItemSliding) {
        let index = this.cart.orders[this.cart.orders.length - 1].orderItems.findIndex(item => item.articleId == article.id);
        this.cart.orders[this.cart.orders.length - 1].orderItems.splice(index, 1);
        this.articles = this.articles.map(arti => {
            if (arti.id == article.id) {
                arti.quantity = 0;
            }
            return arti;
        });
        slidingItem.close();
    }

    public calculator(item_index, article, item: ItemSliding) {
        item.close();
        if (this.cart.orders.length == 0) {
            let alert = this.alertCtrl.create({
                title: 'Cart is empty',
                subTitle: 'To create a new order please go to the "+" button',
                buttons: ['Got it!']
            });
            alert.present();
        } else {
            let index = this.cart.orders[this.cart.orders.length - 1].orderItems.findIndex(item => item.articleId == article.id);
            if (index == -1) {
                let alert = this.alertCtrl.create({
                    title: 'Article not added',
                    subTitle: 'Please add article to customize prices',
                    buttons: ['Got it!']
                });
                alert.present();
            } else {
                let modal = this.modalCtrl.create(OrderCalculatorPage, {article: article});
                modal.onDidDismiss(data => {
                    if (data != false) {
                        this.articles = this.articles.map(arti => {
                            if (arti.id == article.id) {
                                arti.quantity = data.quantity;
                            }
                            return arti;
                        });
                        let index = this.cart.orders[this.cart.orders.length - 1].orderItems.findIndex(item => item.articleId == article.id);
                        this.cart.orders[this.cart.orders.length - 1].orderItems[index].consumerPrice = data.consumerPrice;
                        if (this.cart.orders[this.cart.orders.length - 1].orderItems[index].price != data.customPrice) {
                            this.cart.orders[this.cart.orders.length - 1].orderItems[index].customPrice = data.customPrice;
                        }
                        this.cart.orders[this.cart.orders.length - 1].orderItems[index].discountPercent = data.discountPercent;
                        this.cart.orders[this.cart.orders.length - 1].orderItems[index].quantity = data.quantity;

                        this.cart.orders[this.cart.orders.length - 1].orderItems[index].newPrice = data.newPrice;
                        this.cart.orders[this.cart.orders.length - 1].orderItems[index].marg_pr = data.marg_pr;
                        this.cart.orders[this.cart.orders.length - 1].orderItems[index].marg_kr = data.marg_kr;
                        this.cart.orders[this.cart.orders.length - 1].orderItems[index].tax = data.tax;


                    }
                    ;

                });
                modal.present();
            }
        }
    }

    public getItems(ev: any) {
        this.articles = this.articles_super;

        let val = ev.target.value;

        if (val && val.trim() != '') {
            this.articles = this.articles.filter((article) => {
                return (article.product.toLowerCase().indexOf(val.toLowerCase()) > -1 || article.price.toLowerCase().indexOf(val.toLowerCase()) > -1
                    || article.suppArtNr.toLowerCase().indexOf(val.toLowerCase()) > -1);
            })
        }
    }

    public momentFunc(date, format) {
        return moment(date).format(format)
    }

    public back() {
        this.viewCtrl.dismiss(this.cart);
    }

}



