import { Component } from '@angular/core';
import {NavController, NavParams, ViewController} from 'ionic-angular';

@Component({
    selector: 'page-distribution-filter-2',
    templateUrl: 'distribution-filter-2.html',
})
export class DistributionFilter_2Page {

    public tags:any;
    public tagFilter:any = [];
    // public articleFiltegr:any = [];
    public listingFilter:any = false;
    public groups:any;
    public listings:any;
    public groupFilter:any  = false;
    public articleFilter:any = false;

    constructor(public navCtrl: NavController,
                private viewCtrl: ViewController,
                public navParams: NavParams) {
        this.groups = this.navParams.data.groups;
        this.listings = this.navParams.data.listings;
    }

    public back() {
        this.viewCtrl.dismiss({
            // articleFilter: this.articleFilter,
            listingFilter: this.listingFilter,
            groupFilter: this.groupFilter,
            articleFilter: this.articleFilter
        });
    }

    public apply() {
        this.viewCtrl.dismiss({
            articleFilter: this.articleFilter,
            listingFilter: this.listingFilter,
            groupFilter: this.groupFilter
        })
    }
}



