import { HttpClient, HttpParams } from '@angular/common/http';
import {Component} from '@angular/core';
import {NavController, NavParams, ViewController, AlertController, ActionSheetController} from 'ionic-angular';
import {Storage} from '@ionic/storage';
import { Globals } from '../../providers/globals';

@Component({
    selector: 'page-order-send',
    templateUrl: 'order-send.html',
})
export class OrderSendPage {

    public store: any;
    public email: any;
    public email_list: any = [];
    public contacts: any;
    public user: any;

    constructor(public navCtrl: NavController,
                public viewCtrl: ViewController,
                private http: HttpClient,
                public storage: Storage,
                public alertCtrl: AlertController,
                public actionSheetCtrl: ActionSheetController,
                public navParams: NavParams) {
    }

    ionViewWillEnter() {
        this.store = this.navParams.data.store;
        this.storage.get('user').then((user) => {
            this.user = user;
            let params = new HttpParams()
            .append('id', this.store.id)
            .append('expand', 'storeTags.tag,storeContacts,category,customFieldDatas')
            this.http.get(Globals.api + 'app/store/view', {params}).subscribe((store: any) => {
                this.contacts = store.storeContacts;
            });
        });
    }

    public sendOrder() {
        this.viewCtrl.dismiss(this.email_list);
    }

    public addContact() {
        let actionSheet = this.actionSheetCtrl.create({
            title: 'Add Contact',
            buttons: [
                {
                    text: 'Add from contacts',
                    handler: () => {
                        this.addFromContacts();
                    }
                },
                {
                    text: 'Add manually',
                    handler: () => {
                        this.addManually();
                    }
                },
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: () => {
                    }
                }
            ]
        });
        actionSheet.present();
    }

    private addFromContacts() {
        let alert = this.alertCtrl.create();
        alert.setTitle('Add from contacts');

        for (let key in this.contacts) {
            alert.addInput({
                type: 'checkbox',
                label: this.contacts[key].name,
                value: this.contacts[key].email
            });
        }

        alert.addButton('Cancel');
        alert.addButton({
            text: 'Okay',
            handler: (data: any) => {
                for (let i in data) {
                    this.email_list.push(data[i]);
                }
            }
        });

        alert.present();
    }

    private addManually() {
        let alert = this.alertCtrl.create({
            title: 'Login',
            inputs: [
                {
                    name: 'email',
                    placeholder: 'Email'
                },
            ],
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: data => {
                    }
                },
                {
                    text: 'Add',
                    handler: data => {
                        this.email_list.push(data.email);
                    }
                }
            ]
        });
        alert.present();
    }

    public back() {
        this.viewCtrl.dismiss();
    }

}



