import { HttpClient, HttpParams } from '@angular/common/http';
import {Component} from '@angular/core';
import {
    ActionSheetController,
    Loading, LoadingController, ModalController, NavController, NavParams, Platform,
    ToastController
} from 'ionic-angular';
import {Camera} from "@ionic-native/camera";
import {File} from "@ionic-native/file";
import {FilePath} from "@ionic-native/file-path";
import {Transfer, TransferObject} from "@ionic-native/transfer";
import {VisitPage} from "../visit/visit";
import {DistibutionPhotoCropPage} from "../distibution-photo-crop/distibution-photo-crop";
import {OrderCartPage} from "../order-cart/order-cart";
import {Storage} from "@ionic/storage";
import { Globals } from '../../providers/globals';

declare var cordova: any;

@Component({
    selector: 'page-distribution-photo-add',
    templateUrl: 'distribution-photo-add.html',
})
export class DistributionPhotoAddPage {
    distribution: any;
    store: any;

    public payload: any = [];
    public images: any = [];
    public selected_image;
    public lastImage: string = null;
    public loading: Loading;
    public answer: any = {};
    public dist_images:any = [];
    public user: any;

    constructor(public loadingCtrl: LoadingController,
                public navCtrl: NavController,
                public navParams: NavParams,
                private camera: Camera,
                public actionSheetCtrl: ActionSheetController,
                private transfer: Transfer,
                public toastCtrl: ToastController,
                private file: File,
                private modal: ModalController,
                private filePath: FilePath,
                public http: HttpClient,
                public storage: Storage,
                public platform: Platform) {

        this.images = [];

        storage.get('user').then((user) => {
            this.user = user;
        });

        this.selected_image = 0;
        this.store = this.navParams.data.store;
        this.distribution = this.navParams.data.distribution;
        this.addPhoto(this.navParams.data.type);
        // this.images.push('https://static01.nyt.com/images/2014/05/28/dining/Apple-Pie/Apple-Pie-articleLarge.jpg');
    }

    ionViewDidAppear() {
    }

    private createFileName() {
        let d = new Date(),
            n = d.getTime(),
            newFileName = n + ".jpg";
        return newFileName;
    }

    public selectImage(image) {
        this.selected_image = this.images.indexOf(image);
    }

    public addImage() {
        const actionSheet = this.actionSheetCtrl.create({
            title: 'Select image',
            buttons: [
                {
                    text: 'Gallery',
                    role: 'destructive',
                    handler: () => {
                        this.addPhoto('gallery');
                    }
                },
                {
                    text: 'Take a Photo',
                    handler: () => {
                        this.addPhoto('camera');
                    }
                }
            ]
        });
        actionSheet.present();
    }

    public takePicture(sourceType, payload, field_id) {
        // Create options for the Camera Dialog
        let options = {
            quality: 25,
            sourceType: sourceType,
            saveToPhotoAlbum: true,
            correctOrientation: true
        };

        this.camera.getPicture(options).then((imagePath) => {
            // Special handling for Android library
            if (this.platform.is('android') && sourceType === this.camera.PictureSourceType.PHOTOLIBRARY) {
                this.filePath.resolveNativePath(imagePath)
                    .then(filePath => {
                        let correctPath = filePath.substr(0, filePath.lastIndexOf('/') + 1);
                        let currentName = imagePath.substring(imagePath.lastIndexOf('/') + 1, imagePath.lastIndexOf('?'));
                        this.copyFileToLocalDir(correctPath, currentName, this.createFileName(), payload, field_id);
                    });
            } else {
                let currentName = imagePath.substr(imagePath.lastIndexOf('/') + 1);
                let correctPath = imagePath.substr(0, imagePath.lastIndexOf('/') + 1);
                this.copyFileToLocalDir(correctPath, currentName, this.createFileName(), payload, field_id);
            }
        }, (err) => {
            // this.presentToast("Can't select image");
        });
    }

    private copyFileToLocalDir(namePath, currentName, newFileName, payload, field_id) {
        this.file.copyFile(namePath, currentName, cordova.file.dataDirectory, newFileName).then(success => {
            this.lastImage = newFileName;
            this.uploadImage(payload, field_id);

            // this.images.push(this.pathForImage(this.lastImage));
            
        }, error => {
            this.presentToast("Can't save file");
        });
    }

    private presentToast(text, css = 'default-toast') {
        let toast = this.toastCtrl.create({
            message: text,
            showCloseButton: true,
            closeButtonText: "Ok",
            position: 'middle',
            cssClass: css,
        });
        toast.present();
    }

    public pathForImage(img) {
        if (img === null) {
            return '';
        } else {
            return cordova.file.dataDirectory + img;
        }
    }

    public addPhoto(type) {
        if (type == 'camera') {
            this.takePicture(this.camera.PictureSourceType.CAMERA, null, null);
        }
        if (type == 'gallery') {
            this.takePicture(this.camera.PictureSourceType.PHOTOLIBRARY, null, null);
        }
    }

    public uploadImage(payload, field_id) {

        // Destination URL
        let url = "http://207.154.244.178/web/index.php/mobile-api/upload-image-cropped"; // TODO
        let url2 = "http://207.154.244.178/web/index.php/mobile-api/upload-image-thumbnail"; // TODO
        // File for Upload
        let targetPath = this.pathForImage(this.lastImage);
// file_name
        // File name only
        let filename = this.lastImage;

        let cropModal = this.modal.create(DistibutionPhotoCropPage, {
            image: targetPath
        });
        cropModal.onDidDismiss(data => {
            let options = {
                fileKey: "file",
                fileName: filename,
                chunkedMode: false,
                mimeType: "multipart/form-data",
                params: {
                    'fileName': filename,
                    'cropInfo': data.cropInfo
                }
            };

            const fileTransfer: TransferObject = this.transfer.create();

            this.loading = this.loadingCtrl.create({
                content: 'Uploading' + '...',
            });
            this.loading.present();


            fileTransfer.upload(targetPath, url, options).then(data => {
                let json = JSON.parse(data.response);
                // this.payload.push(json.file_name);
                this.images.push(json.file_url);// TODO image url for saving store here
                this.dist_images.push(json.file_name);
                this.presentToastBottom('Upload image');

                this.loading.dismissAll();
            }, err => {
                this.loading.dismissAll();
                this.presentToast('Try later');
            });

            fileTransfer.upload(targetPath, url2, options).then(data => {

            }, err => {

            });
        });
        cropModal.present();
    }

    private presentToastBottom(text, css = 'default-toast') {
        let toast = this.toastCtrl.create({
            message: text,
            position: 'bottom',
            duration: 3000,
            cssClass: css,
        });
        toast.present();
    }

    public saveDistribution() {
        let answers = [];

        for (let distributionArticle in this.distribution.distributionArticles) {
            let quantity = 0;
            if (this.answer[this.distribution.distributionArticles[distributionArticle].article.id]) {
                quantity = 1;
            }
            answers.push({
                'article_id': this.distribution.distributionArticles[distributionArticle].article.id,
                'store_id': this.store.id,
                'quantity': quantity
            });
        }
        let post = {};
        post['distributions'] = answers;
        post['photos'] = this.dist_images;
        let params = new HttpParams().append('id', this.distribution.id);
        this.http.post(Globals.api + 'app/distribution/register', post, {params}).subscribe(distribution => {
            this.navCtrl.pop();
        });
    }

    public cropImage(targetPath, item_index) {
        console.log("blah");
        let url = "http://207.154.244.178/web/index.php/mobile-api/upload-image-cropped"; // TODO
        //
        let cropModal = this.modal.create(DistibutionPhotoCropPage, {
            image: targetPath
        });
        cropModal.onDidDismiss(data => {
            let options = {
                fileKey: "file",
                fileName: targetPath,
                chunkedMode: false,
                mimeType: "multipart/form-data",
                params: {
                    'fileName': targetPath,
                    'cropInfo': data.cropInfo
                }
            };

            const fileTransfer: TransferObject = this.transfer.create();

            this.loading = this.loadingCtrl.create({
                content: 'Cropping image' + '...',
            });
            this.loading.present();


            fileTransfer.upload(targetPath, url, options).then(data => {
                let json = JSON.parse(data.response);
                // this.payload.push(json.file_name);
                this.images[item_index] = json.file_url;// TODO image url for saving store here
                this.dist_images.push(json.file_name);
                this.presentToastBottom('Upload image');

                this.loading.dismissAll();
            }, err => {
                this.loading.dismissAll();
                this.presentToast('Try later');
            });
        });
        cropModal.present();
    }
}



