import { HttpClient, HttpParams } from '@angular/common/http';
import { Visit } from './../app/models/visit';
import {Injectable} from '@angular/core';
import { RequestOptions, Response} from "@angular/http";
import {Observable} from "rxjs/Observable";
import { Globals } from './globals';

@Injectable()
export class VisitService {

    constructor(private http: HttpClient) {
    }

    fetchAll(params?: HttpParams): Observable<Visit[]> {
        return this.http.get(Globals.api + 'app/visit/list', {params}).map((res: any[]) => Visit.fromJSONArray(res));
    }

    create(payload: any, params?: HttpParams): Observable<Visit> {
        return this.http.post(Globals.api + 'app/visit/create', payload, {params}).map((res: any) => Visit.fromJSONObject(res));
    }

    update(id: number, payload: any, params: HttpParams = new HttpParams()): Observable<Visit> {
        params = params.append('id', id.toString());
        return this.http.put(Globals.api + 'app/visit/update', payload, {params}).map((res: any) => Visit.fromJSONObject(res));
    } 

    delete(id: number, params : HttpParams = new HttpParams()): Observable<Object> {
        params = params.append('id', id.toString());        
        return this.http.get(Globals.api + 'app/visit/delete', {params});
    }
}


