import { AuthService } from './auth.service';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Globals } from './globals';

@Injectable()
export class AuthenticationService {

    constructor(private http: HttpClient, private storage: Storage, private authService: AuthService) {}

    login(username: string, password: string): Observable<boolean> {
        let payload = {
            LoginForm: {
                username: username,
                password: password
            }
        };
        return this.http.post(Globals.api + 'user/login', payload)
            .map((data: any) => {
                // login successful if there's a jwt token in the response
                let token = data && data['auth_token'];
                if (token) {
                    // manage accesses
                    let accesses = {};
                    for (let index in data['seller_accesses']) {
                        accesses[parseInt(data['seller_accesses'][index].access_id)] = true;
                    }

                    for (let index in [1, 2, 3, 4, 5]) {
                        var i = parseInt(index) + 1;
                        if (!accesses.hasOwnProperty(i)) {
                            accesses[i] = false;
                        }
                    }
                    data.accesses = accesses
                    this.storage.set('user', data);
                    this.authService.init(data);
                    // return true to indicate successful login
                    return true;
                } else {
                    // return false to indicate failed login
                    return false;
                }
            });
    }

    logout(): void {
        // clear token remove user from local storage to log user out
        this.storage.remove('user');
    }
}


