import {Component} from '@angular/core';
import {App, LoadingController, NavController, NavParams} from 'ionic-angular';
import {OrderProvider} from "../../providers/order/order";
import {OrderHistoryDetailsPage} from "../order-history-details/order-history-details";
import * as moment from 'moment';

@Component({
    selector: 'page-store-details-history',
    templateUrl: 'store-details-history.html',
})
export class StoreDetailsHistoryPage {

    public store: any;
    public orderHistory: any;
    public orderFormation:any;
    public orderHistorySuper:any;
    public objectKeys = Object.keys;

    constructor(public navCtrl: NavController,
                private _orderProvider: OrderProvider,
                private loader: LoadingController,
                public app: App,
                public navParams: NavParams) {
    }

    ionViewWillEnter() {
        this.store = this.navParams.data;

        let loader = this.loader.create({
            content: 'Loading order history'
        });

        loader.present().then(() => {
            this._orderProvider.getOrderHistory({
                expand: 'orderType0,orderTypeStatus0,store,orderItems.article',
                storeId: this.store.id
            })
                .subscribe(orderHistory => {
                    this.orderHistorySuper = orderHistory;
                    this.orderHistory = orderHistory;
                    this.orderHistory = this.groupBy(this.orderHistory, 'deliveryDate');
                    loader.dismissAll();
                });
        });
    }

    public orderDetails(order) {
        this.navCtrl.push(OrderHistoryDetailsPage, order);
    }

    private groupBy(array, prop) {
        return array.reduce(function (groups, item) {
            const val = item[prop];
            groups[val] = groups[val] || [];
            groups[val].push(item);
            return groups
        }, {})
    }

    public momentFunc(date, format) {
        return moment(date).format(format)
    }

    public back() {
        this.app.navPop();
    }

    public getItems(ev: any) {
        this.orderFormation = this.orderHistorySuper;

        let val = ev.target.value;

        if (val && val.trim() != '') {
            this.orderFormation = this.orderFormation.filter((article) => {
                if (article.orderType0) {
                    return (article.note.toLowerCase().indexOf(val.toLowerCase()) > -1 || article.orderType0.name.toLowerCase().indexOf(val.toLowerCase()) > -1
                    );
                }
            });
        }
        this.orderHistory = this.groupBy(this.orderFormation, 'deliveryDate');
    }

    // TODO filter, detail, calculator, validation of new order, build, group by, moment setup
}



