import {Component} from '@angular/core';
import {NavController, NavParams} from 'ionic-angular';
import {OrderHistoryItemDetailsPage} from "../order-history-item-details/order-history-item-details";

@Component({
    selector: 'page-order-history-details',
    templateUrl: 'order-history-details.html',
})
export class OrderHistoryDetailsPage {

    public order:any;

    constructor(public navCtrl: NavController,
                public navParams: NavParams) {
    }

    ionViewWillEnter() {
        this.order = this.navParams.data;

    }

    public itemDetails(item) {
        this.navCtrl.push(OrderHistoryItemDetailsPage, item);
    }

    public parseFloat2(item) {
        return parseFloat(item);
    }

}



