import { AuthService } from './../../providers/auth.service';
import { HttpParams } from '@angular/common/http';
import { DashboardService } from './../../providers/dashboard.service';
import { Component } from '@angular/core';
import { NavController, NavParams, MenuController, LoadingController } from 'ionic-angular';
import { ChartOptions } from '../../providers/chart.options';
import { WidgetType } from '../../app/models/widget-type.enum';
import { DistributionSecondPage } from '../distribution-second/distribution-second';
import { Globals } from '../../providers/globals';

/**
 * Generated class for the DashboardFirstPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-dashboard-first',
  templateUrl: 'dashboard-first.html',
})
export class DashboardFirstPage {
  public salesPeriod = "current";
  public dashboardsME = {
    distributions: []
  };
  public dashboardsTEAM = {
    distributions: []
  };
  public loadingTEAM = true;
  public chartOptions: any = ChartOptions;
  public activeSegment = 'me';
  public globals = Globals;
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    private menuCtrl: MenuController,
    private dashboardService: DashboardService,
    public loader: LoadingController,
    private authService: AuthService,
  ) {
    let loading = this.loader.create({
      content: 'Loading dashboards'
    });
    loading.present().then(() => {
      let params = new HttpParams()
      .append('salesPeriod', this.salesPeriod)
      .append('seller', authService.getSeller().id);
      this.dashboardService.fetchDistributions(params).subscribe((items: any[]) => {
        this.dashboardsME.distributions = items;
        // console.log(this.dashboardsME.distributions);
        // this.dashboardsME.distributions = JSON.parse(localStorage.getItem('tempdists'));
        this.dashboardsME.distributions.forEach(distribution => {
          this.prepareChartData(distribution, WidgetType.DISTRIBUTION);
        });
        // console.log(items);
        // localStorage.setItem('tempdists', JSON.stringify(items));
        loading.dismiss();
      });
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DashboardFirstPage');
    // let params = new HttpParams()
    // .append('salesPeriod', this.salesPeriod);
    // this.dashboardService.fetchDistributions(params).subscribe((items: any[]) => {
    //   this.dashboardsTEAM.distributions = items;
    //   this.dashboardsTEAM.distributions.forEach(distribution => {
    //     this.prepareChartData(distribution, WidgetType.DISTRIBUTION);
    //   });
    //   this.loadingTEAM = false;
    // });
  }


  onDistributionClicked(distribution) {
    this.navCtrl.push(DistributionSecondPage, { id: distribution.id });
  }

  private prepareChartData(item: any, type: WidgetType) {
    switch (type) {
      case WidgetType.DISTRIBUTION: {
        let milestone = 0,
          outcome = 0,
          start_outcome = 0,
          sold = 0,
          checked = 0;
        item.sellers.forEach(seller => {
          milestone += seller.milestone;
          outcome += seller.outcome;
          start_outcome += seller.start_outcome;
          sold += seller.sold;
          checked += seller.checked;
        });
        item.milestone = milestone;
        item.outcome = outcome;
        item.sold = sold;
        item.checked = checked;
        item.start_outcome = start_outcome;
        var index;
        if (milestone === 0 || outcome === 0) {
          outcome = 0;
          index = 100;
        } else {
          index = milestone - outcome;
          if (index < 0) index = 0;
        }

        var indexChecked;
        if (sold === 0 || checked === 0) {
          sold = 0;
          indexChecked = 100;
        } else {
          indexChecked = checked - sold;
          if (indexChecked < 0) indexChecked = 0;
        }

        item.chartData = [
          [outcome, index],
          [sold, indexChecked]
        ];
        item.sellers.forEach(seller => {
          seller.index = 0;
          if (seller.milestone > 0) seller.index = seller.outcome / seller.milestone;
        });
      }
    }
  }

  firstFive(item: any, index) {
    return index < 5;
  }
}



