import {Component} from '@angular/core';
import {NavController, NavParams, ViewController} from 'ionic-angular';

@Component({
    selector: 'page-distribution-answer-detail',
    templateUrl: 'distribution-answer-detail.html',
})
export class DistributionAnswerDetailPage {

    constructor(public navCtrl: NavController,
                public viewCtrl: ViewController,
                public navParams: NavParams) {
    }

    public distribution_answer:any;

    ionViewWillEnter() {
        console.log("HERE");
        console.log(this.navParams.data);
        this.distribution_answer = this.navParams.data.distribution_answer;
    }

    public back() {
        this.viewCtrl.dismiss();
    }

}



