import { HttpClient, HttpParams } from '@angular/common/http';
import {Injectable} from '@angular/core';
import { Globals } from './globals';

@Injectable()
export class DashboardService {

    constructor(private http: HttpClient) {
    }

    fetchDistributions(params?: HttpParams) {
        return this.http.get(Globals.api + 'dashboardApi/distribution', {params: params});
    }

    fetchDistribution(id: number, params?: HttpParams) {
        return this.http.get(Globals.api + 'dashboardApi/distribution/' + id, {params: params});
    }

    fetchDistributionReport(id: number, params?: HttpParams) {
        return this.http.get(Globals.api + 'dashboardApi/distributionReport/' + id, {params: params});
    }
}


