import { HttpClient, HttpParams } from '@angular/common/http';
import {LoadingController, NavController, NavParams, ViewController} from 'ionic-angular';
import {Component} from '@angular/core';
import {Storage} from '@ionic/storage';
import {VisitStoresPage} from '../visit-stores/visit-stores';
import * as moment from 'moment';
import {StoreDetailsPage} from "../store-details/store-details";
import { StoreDetailsActionsPage } from '../store-details-actions/store-details-actions';
import { CommonService } from '../../providers/common.service';
import { Globals } from '../../providers/globals';
/**
 * Generated class for the VisitPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
    selector: 'page-visit',
    templateUrl: 'visit.html'
})
export class VisitPage {
    public user: any;
    public stores: any;
    public types: any;
    public callback: any;
    public visit: any = {
        visit_type_id: undefined,
        store: undefined,
        booked:0,
        note:'',
        duration:0,
        start:undefined
    };
    /*
     booked
     :
     0
     duration
     :
     60
     start
     :
     "2017-05-02 06:00:00"
     store_id
     :
     "165916"
     title
     :
     "CITYGROSS SOLLENTUNA"
     * */

    constructor(public storage: Storage,
                public navCtrl: NavController,
                public http: HttpClient,
                public commonService: CommonService,
                public loader: LoadingController,
                public navParams: NavParams,
                public viewCtr: ViewController) {

        this.user = navParams.get('user');
        this.stores = navParams.get('stores');
        this.callback = navParams.get('callback');
        let visit = navParams.get('visit');
        console.log(visit);

        let stores_loader = this.loader.create({
            content: 'Loading types'
        });

        this.visit.start = navParams.get('datetime');
        if(this.visit.start){
            this.selectStore();
        }

        stores_loader.present().then(() => {
            this.commonService.fetchVisitTypes().subscribe(types => {
                this.types = types;
                if(visit) {
                    this.visit = visit;
                    for(let type of types){
                        if(this.visit.visit_type_id==type.id) this.visit.type = type;
                    }
                    this.visit.start = moment(this.visit.start).format();
                }
                console.log(this.visit);
                stores_loader.dismiss();
            });
        });

    }

    storeActions() {
        this.navCtrl.push(StoreDetailsActionsPage,{store:this.visit.store})
    }

    public storeDetails() { // TODO here
        console.log(this.visit.store);
        this.navCtrl.push(StoreDetailsPage,{store:this.visit.store})
    }

    selectStore() {
        this.navCtrl.push(VisitStoresPage, {
            stores: this.stores, callback: store => {
                this.visit.store = store;
            }
        });

    }

    saveVisit() {
        let visit = {
            id:this.visit.id,
            booked:this.visit.booked,
            duration:this.visit.duration,
            note:this.visit.note,
            start:this.visit.start,
            visit_type_id:this.visit.type?this.visit.type.id:0,
            store_id:this.visit.store.id,
            title:this.visit.store.customer,
        };
        if(visit.id){
            let params = new HttpParams().append('id', visit.id)
            this.http.put(Globals.api + 'app/visit/update', visit, {params}).subscribe(() => {
                this.navCtrl.pop({}, this.callback());
            })
        } else {
            this.http.put(Globals.api + 'app/visit/create', visit).subscribe(() => {
                this.navCtrl.pop({}, this.callback());
            })
        }
    }

    deleteVisit(){
        let params = new HttpParams().append('id', this.visit.id);
        this.http.get(Globals.api + 'app/visit/delete', {params}).subscribe(responce => {
            this.navCtrl.pop({}, this.callback());
        });
    }
    ionViewDidLoad() {
        console.log('ionViewDidLoad VisitPage');
    }

    closeModal() {
        this.navCtrl.pop();
    }
}



