import {Component} from '@angular/core';
import {AlertController, NavController, NavParams, ViewController} from 'ionic-angular';


@Component({
    selector: 'page-order-formation-add-order',
    templateUrl: 'order-formation-add-order.html',
})
export class OrderFormationAddOrderPage {

    public order_types: any = [];
    public o_t = [];
    public params:any = {
        deliveryDate: null,
        orderTypeId: null,
        orderTypeName: null,
        orderItems: []
    };

    constructor(public navCtrl: NavController,
                public viewCtrl: ViewController,
                private alertCtrl: AlertController,
                public navParams: NavParams) {
    }

    ionViewWillEnter() {
        for (let key in this.navParams.data.order_types) {
            this.order_types.push(this.navParams.data.order_types[key]);
            this.o_t.push(this.navParams.data.order_types[key]);
        }
        this.order_types = this.order_types.map(order_type => {
            return {
                value: order_type.id,
                text: order_type.name
            }
        });
    }

    public createOrder() {
        if (this.params.deliveryDate != null && this.params.orderTypeId != null) {

            let found = this.o_t.findIndex(order_type => order_type.id == this.params.orderTypeId);
            this.params.orderTypeName = this.o_t[found].name;
            this.viewCtrl.dismiss(this.params);
        } else {
            let alert = this.alertCtrl.create({
                title: 'All fields required',
                subTitle: 'Please fill out all fields',
                buttons: ['Ok']
            });
            alert.present();
        }
    }

    public dismiss() {
        this.viewCtrl.dismiss();
    }
}





