import { Injectable } from "@angular/core";
import {
    HttpInterceptor,
    HttpRequest,
    HttpHandler,
    HttpEvent,
    HttpParams
} from "@angular/common/http";
import { AuthService } from "./auth.service";
import { Observable } from 'rxjs/Observable';

@Injectable()
export class RequestInterceptorService implements HttpInterceptor {

    constructor(private authService: AuthService) { }

    addToken(req: HttpRequest<any>, token: string): HttpRequest<any> {
        let newParams = new HttpParams({ fromString: req.params.toString() });
        newParams = newParams.append('auth_token', token);
        newParams = newParams.append('api', '1');
        newParams = newParams.append('response_type', 'application/json');
        
        return req.clone({ params: newParams });
    }

    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        return this.authService.getToken().flatMap(token => {
            return next.handle(this.addToken(req, this.authService.token));
        });
    }
}



