import { HttpClient, HttpParams } from '@angular/common/http';
import {Component, ViewChild} from '@angular/core';
import {LoadingController, AlertController, ViewController, NavController, NavParams, ToastController, ModalController, App} from 'ionic-angular';
import {Storage} from '@ionic/storage';
import {SignaturePad} from 'angular2-signaturepad/signature-pad';

import {GalleryModal} from 'ionic-gallery-modal';
import * as moment from 'moment';
import { Globals } from '../../providers/globals';

@Component({
    selector: 'page-form',
    templateUrl: 'form.html',
})
export class FormPage {
    signature = '';
    isDrawing = false;
    active_page: string = 'action';

    @ViewChild(SignaturePad) signaturePad: SignaturePad;
    public signaturePadOptions: Object = { // Check out https://github.com/szimek/signature_pad
        'minWidth': 2,
        'canvasWidth': 400,
        'canvasHeight': 200,
        'backgroundColor': '#f6fbff',
        'penColor': '#666a73'
    };
    public store: any;
    public action: any;
    public answer: any;
    public history: any = [];
    public field_answer: any = {};
    public input_id:any = false;

    constructor(public app: App,
                public navCtrl: NavController,
                public toastCtrl: ToastController,
                public navParams: NavParams,
                public loader: LoadingController,
                public http: HttpClient,
                public alertCtrl: AlertController,
                public storage: Storage,
                public viewCtrl: ViewController,
                public  modalCtrl: ModalController) {

        this.action = navParams.get('action');
        this.store = navParams.get('store');
        this.answer = {
            data: [],
            form_id: this.action.id,
            store_id: this.store.id
        };
        this.getAction();
    }

    ionViewDidEnter() {
        if (this.signaturePad) {
            this.signaturePad.clear()
            this.storage.get('savedSignature').then((data) => {
                this.signature = data;
            });
        }

    }

    openGallery(sliderImages) {
        let photos = [];
        for (let sliderImage of sliderImages) {
            photos.push({url: 'https://s3.keysale.se/files/' + sliderImage.key});
        }
        let modal = this.modalCtrl.create(GalleryModal, {
            photos: photos,
            initialSlide: []
        });
        modal.present();
    }

    saveAction(newAnswer?) {
        let save_load = this.loader.create({
            content: 'Saving Answer'
        });
        save_load.present().then(() => {
            this.answer.data = [];
            for (let field_id in this.field_answer) {
                if (this.field_answer[field_id]) {
                    if (this.field_answer[field_id].constructor == Object) {
                        var options = [];
                        for (let option_id in this.field_answer[field_id]) {
                            if (this.field_answer[field_id][option_id]) {
                                options.push(option_id);
                            }
                        }
                        this.answer.data.push({field_id: field_id, value: options.join('_')});
                    } else this.answer.data.push({field_id: field_id, value: this.field_answer[field_id]});
                } else this.answer.data.push({field_id: field_id, value: null});
            }
            if (this.input_id != false) {
                let params = new HttpParams().append('id', this.input_id);
                this.http.post(Globals.api + 'app/answer/update', this.answer, {params}).subscribe(action => {
                    if (newAnswer == true) {
                        this.newAnswer();
                        let params = new HttpParams()
                        .append('action', this.action.id)
                        .append('expand', 'datas')
                        .append('store', this.store.id)
                        this.http.post(Globals.api + 'app/answer', this.answer, {params}).subscribe(answers => {
                            this.history = answers;
                            save_load.dismissAll();
                        });
                    } else {
                        save_load.dismissAll()
                        this.navCtrl.pop();
                        this.input_id = false;
                    }
                });

            } else {
                let params = new HttpParams()
                        .append('id', this.action.id)
                        .append('expand', 'datas')
                        .append('store', this.store.id)
                this.http.post(Globals.api + 'app/answer/create', this.answer, {params}).subscribe(action => {
                    if (newAnswer == true) {
                        this.newAnswer();
                        let params = new HttpParams()
                        .append('action', this.action.id)
                        .append('expand', 'datas')
                        .append('store', this.store.id)
                        this.http.post(Globals.api + 'app/answer', this.answer, {params}).subscribe(answers => {
                            this.history = answers;
                            save_load.dismissAll()
                        });
                    } else {
                        save_load.dismissAll()
                        this.navCtrl.pop();
                    }
                });
            }
        });
    }

    public newAnswer() {
        this.answer = {
            data: [],
            form_id: this.action.id,
            store_id: this.store.id
        };



        for (let field of this.action.fields) {
            if (field.type_id == 3) {
                this.field_answer[field.id] = {};
                for (let option of field.options) {
                    this.field_answer[field.id][option.id] = null;
                }
            } else this.field_answer[field.id] = null;
        }
        this.input_id = false;
    }

    getAction() {
        let actions_loader = this.loader.create({
            content: 'Loading ' + this.action.name
        });
        actions_loader.present().then(() => {
            let params = new HttpParams()
            .append('expand', 'fields.options,fields.sliderImages,formSettings')
            .append('id', this.action.id);
            this.http.get(Globals.api + 'app/actions/view', {params}).subscribe((action: any) => {
                for (let field of action.fields) {
                    if (field.type_id == 3) {
                        this.field_answer[field.id] = {};
                        for (let option of field.options) {
                            this.field_answer[field.id][option.id] = null;
                        }
                    } else this.field_answer[field.id] = null;
                }
                this.action = action;
                actions_loader.dismissAll()
                let params = new HttpParams()
                .append('expand', 'datas')
                .append('store', this.store.id)
                .append('action', this.action.id)
                this.http.post(Globals.api + 'app/answer', this.answer, {params}).subscribe(answers => {
                    this.history = answers;
                    if (this.history.length > 0) {
                        this.openAnswer(this.history[0]);
                    }
                });
            });
        })
    }

    openAnswer(answer) {
        console.log(answer);
        console.log(this.history);
        this.input_id = answer.id;
        this.answer = answer;
        for (let data of this.answer.datas) {
            for (let field of this.action.fields) {
                if (data.field_id == field.id) {
                    if (field.type_id == '3') {
                        let options = data.value.split('_');
                        for (let option of options) {
                            this.field_answer[data.field_id][parseInt(option)] = true;
                        }
                    } else if (field.type_id == '4') {
                        this.field_answer[data.field_id] = data.option_id;
                    } else if (field.type_id == '5') {
                        this.field_answer[data.field_id] = data.option_id;
                    } else {
                        this.field_answer[data.field_id] = data.value;
                    }
                }
            }

        }
        this.active_page = 'action'
    }

    drawComplete(field) {
        this.isDrawing = false;
        this.field_answer[field.id] = this.signaturePad.toDataURL();
    }

    drawStart() {
        this.isDrawing = true;
    }

    savePad() {
        this.signature = this.signaturePad.toDataURL();
        this.storage.set('savedSignature', this.signature);
        this.signaturePad.clear();
        let toast = this.toastCtrl.create({
            message: 'New Signature saved.',
            duration: 3000
        });
        toast.present();
    }

    clearPad() {
        this.signaturePad.clear();
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad FormPage');
    }

    public now(field_id) {
        console.log(moment(new Date()).format('HH:mm'));
        this.field_answer[field_id] = moment(new Date()).format('HH:mm');
        console.log(this.field_answer[field_id]);
    }

    public back() {
        let alert = this.alertCtrl.create({
            title: 'Go back',
            message: 'Do you want to go back without saving answer?',
            buttons: [
                {
                    text: 'Yes',
                    handler: () => {
                        this.viewCtrl.dismiss();
                        console.log('Buy clicked');
                    }
                },
                {
                    text: 'No',
                    role: 'cancel',
                    handler: () => {
                        console.log('Cancel clicked');
                    }
                }

            ]
        });
        alert.present();
    }



}



