import {Injectable} from '@angular/core';
import {Api} from "../api/api";

@Injectable()
export class OrderProvider {

    constructor(private _api: Api) {
    }

    /**
     * @desc:
     * @request_method: GET
     * @params:
     */
    public getArticleGroups(params) {
        return this._api.get('site/articleGroups', params);
    }

    /**
     * @desc:
     * @request_method: GET
     * @params: expand;
     */
    public getArticles(params) {
        return this._api.get('article', params);
    }

    /**
     * @desc:
     * @request_method: GET
     * @params: expand;
     */
    public getLazyArticles(params) {
        return this._api.get('article/lazyList', params);
    }

    /**
     * @desc:
     * @request_method: GET
     * @params: indexBy;
     */
    public getOrderTypes(params) {
        return this._api.get('site/orderTypes', params);
    }

    /**
     * @desc:
     * @request_method: GET
     * @params: store_id;
     */
    public getListings(params) {
        return this._api.get('order/getListings', params);
    }

    /**
     * @desc:
     * @request_method: GET
     * @params: store_id;
     */
    public getPricing(params) {
        return this._api.get('order/getPricing', params);
    }

    /**
     * @desc:
     * @request_method: POST
     * @payload: ;
     */
    public sendOrder(payload, store) {
        return this._api.post('order/makeOrder?storeId='+store.id, payload);
    }

    /**
     * @desc:
     * @request_method: GET
     * @payload: ;
     */
    public checkDuplicateOrder(params) {
        return this._api.get('order/checkDuplicateOrder', params);
    }

    /**
     * @desc:
     * @request_method: GET
     * @params:
     */
    public getOrderHistory(params) {
        return this._api.get('order/list', params);
    }


}



