import {Component} from '@angular/core';
import {
    ItemSliding, LoadingController, ModalController, NavController, NavParams, ToastController,
    ViewController
} from 'ionic-angular';
import {OrderSendPage} from "../order-send/order-send";
import {OrderProvider} from "../../providers/order/order";
import * as moment from 'moment';
import {OrderCartEditPage} from "../order-cart-edit/order-cart-edit";


@Component({
    selector: 'page-order-cart',
    templateUrl: 'order-cart.html',
})
export class OrderCartPage {

    public cart: any;
    public store: any;

    constructor(public navCtrl: NavController,
                public modalCtrl: ModalController,
                private _orderProvider: OrderProvider,
                private loader: LoadingController,
                private toastCtrl: ToastController,
                public viewCtrl: ViewController,
                public navParams: NavParams) {
    }

    ionViewWillEnter() {
        this.cart = this.navParams.data.cart;
        this.store = this.navParams.data.store;
    }

    public sendOrder() {
        let modal = this.modalCtrl.create(OrderSendPage, {
            store: this.store
        });
        modal.onDidDismiss(data => {
            if (data != null) {
                this.cart.shareEmails = data.join();

                let loader = this.loader.create({
                    content: 'Sending Order'
                });
                loader.present().then(() => {
                    this._orderProvider.sendOrder(this.cart, this.store)
                        .subscribe(order => {
                            let toast = this.toastCtrl.create({
                                message: 'Order sent successfully',
                                duration: 3000,
                                position: 'middle'
                            });

                            toast.present();

                            this.navCtrl.popTo(this.navCtrl.getByIndex(this.navCtrl.length() - 3));
                            loader.dismissAll();
                        });
                });
            }
        });
        modal.present();
    }

    public emptyCart() {
        this.cart = {
            orders: [],
            shareEmails: ""
        };
    }

    public deleteOrderItem(order, orderItem) {
        this.cart.orders[order].orderItems.splice(orderItem, 1);
        if (this.cart.orders[order].orderItems.length == 0) {
            this.cart.orders.splice(order, 1);
        }
    }

    public editOrderItem(order, orderItem, item: ItemSliding) {
        item.close();

        let modal = this.modalCtrl.create(OrderCartEditPage, {item: this.cart.orders[order].orderItems[orderItem]});
        modal.onDidDismiss(data => {
            if (data != false) {
                this.cart.orders[order].orderItems[orderItem].consumerPrice = data.consumerPrice;
                if (this.cart.orders[order].orderItems[orderItem].price != data.customPrice) {
                    this.cart.orders[order].orderItems[orderItem].customPrice = data.customPrice;
                }
                this.cart.orders[order].orderItems[orderItem].discountPercent = data.discountPercent;
                this.cart.orders[order].orderItems[orderItem].quantity = data.quantity;

                this.cart.orders[order].orderItems[orderItem].newPrice = data.newPrice;
                this.cart.orders[order].orderItems[orderItem].marg_pr = data.marg_pr;
                this.cart.orders[order].orderItems[orderItem].marg_kr = data.marg_kr;
                this.cart.orders[order].orderItems[orderItem].tax = data.tax;
            }
        });
        modal.present();

    }

    public momentFunc(date, format) {
        return moment(date).format(format)
    }

    public back() {
        this.viewCtrl.dismiss(this.cart);
    }
}



