import { Component, Input } from '@angular/core';

@Component({
    selector: 'progress-bar',
    templateUrl: 'progress-bar.html'
})
export class ProgressBarComponent {

    @Input('progress') progress;
    @Input('total_article') total_article;
    @Input('total_distribution') total_distribution;
    @Input('total_distribution_number') total_distribution_number;

    constructor() {

    }

    public filterMissing() {
        console.log("blah");
    }

}


