import * as moment from 'moment';
import { VisitType } from "./visit-type";

export class Visit {
    public id: number;
    public storeId: number;
    public title: string;
    public note: string;
    public visit_type_id: number;
    public start: Date;
    public end: Date;
    public status: boolean;
    public booked: boolean;
    public visitType: VisitType;
    public store: any;
    constructor(id: number, storeId: number, title: string, note: string, visit_type_id: string, start: string, duration: number, status: string, booked: string) {
        this.id = id;
        this.storeId = storeId;
        this.title = title;
        this.note = note;
        this.visit_type_id = +visit_type_id;
        this.start = moment(start).toDate();
        this.end = moment(start).add(duration, 'minutes').toDate();
        this.status = !!+status;
        this.booked = !!+booked;
    }

    static fromJSONArray(array: Array<any>): Visit[] {
        return array.map(o => this.fromJSONObject(o));
    }

    static fromJSONObject(o: any): Visit {
        let visit = new Visit(o.id, o.store_id, o.title, o.note, o.visit_type_id, o.start, o.duration, o.status, o.booked);
        if(o.visitType) {
            visit.visitType = VisitType.fromJSONObject(o.visitType);
        }
        if(o.store) {
            visit.store = o.store;
        }
        return visit;
    }
}


