import { Component } from '@angular/core';
import {NavController, NavParams, ViewController} from 'ionic-angular';

@Component({
  selector: 'page-distribution-filter',
  templateUrl: 'distribution-filter.html',
})
export class DistributionFilterPage {

    public groups:any;
    public listings:any;
    public groupFilter:any  = "";
    public listingFilter:any = "";


    constructor(public navCtrl: NavController,
                private viewCtrl: ViewController,
                public navParams: NavParams) {
        this.groups = this.navParams.data.groups;
        this.listings = this.navParams.data.listings;
    }

    public back() {
        this.viewCtrl.dismiss({
            listingFilter: this.listingFilter,
            groupFilter: this.groupFilter
        });
    }

    public apply() {
        this.viewCtrl.dismiss({
            listingFilter: this.listingFilter,
            groupFilter: this.groupFilter
        })
    }
}



