import { VisitService } from './../../providers/visit.service';
import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import * as moment from 'moment';
import { Storage } from '@ionic/storage';
/**
 * Generated class for the PlanVisitPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-plan-visit',
  templateUrl: 'plan-visit.html',
})
export class PlanVisitPage {
  visit: any = {
    start: new Date(),
    note: null,
  };
  store: any = null;
  dateTimePickerOptions: any = {
    timeFormat: 'H:ii',
    dateFormat: 'd MM yy',
    dateWheels: '|D M d|'
  };

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    private visitService: VisitService,
    private storage: Storage
  ) {
    this.store = navParams.data.store;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PlanVisitPage');
  }

  planVisit() {
    let payload = {
      store_id: this.store.id,
      title: this.store.customer,
      booked: 0,
      start: moment(this.visit.start).format('YYYY-MM-DD HH:mm:ss'),
      duration: 60,
      note: this.visit.note
    };
    this.visitService.create(payload).subscribe(() => {
      this.navCtrl.pop();
    });
  }

}



