import {Component} from '@angular/core';
import {App, Events, ModalController, Nav, NavController, NavParams} from 'ionic-angular';
import {OrderProvider} from "../../providers/order/order";
import {OrderFormationPage} from "../order-formation/order-formation";
import {OrderFormationAddOrderPage} from "../order-formation-add-order/order-formation-add-order";

@Component({
    selector: 'page-store-details-orders',
    templateUrl: 'store-details-orders.html',
})
export class StoreDetailsOrdersPage {

    public article_groups:any;
    public store:any;
    public order:any = false;
    public new_order:any = false;
    public cart:any = {
        orders:	[],
        shareEmails: ""
    };

    constructor(
                public navParams: NavParams,
                public nav: NavController,
                private app: App,
                private modalCtrl: ModalController,
                private events: Events,
                private _orderProvider: OrderProvider) {
    }

    ionViewWillEnter() {
        this.store = this.navParams.data;
        this._orderProvider.getArticleGroups({bla: 0})
            .subscribe(article_groups => {
                this.article_groups = article_groups
            });
    }

    public selectArticleGroup(article_group) {
        let modal = this.modalCtrl.create(OrderFormationPage, {
            article_group: article_group,
            store: this.store,
            cart: this.cart
        });
        modal.onDidDismiss(cart => {
            if (typeof cart == 'undefined') {
                this.cart = {
                    orders:	[],
                    shareEmails: ""
                };
            } else {
                this.cart = cart;
            }
        });
        modal.present();

    }

    public back() {
        this.app.navPop();
    }
}



