export class Region {
    public id: number;
    public name: string;
    public salesManagerId: number;
    public manager: any;
    constructor(id: number, name: string, salesManagerId: number) {
        this.id = id;
        this.name = name;
        this.salesManagerId = salesManagerId;
    }
    static fromJSONObject(o: any): Region {
        let region = new Region(o.id, o.name, o.sales_manager_id);
        if(o.manager) {
            region.manager = o.manager;
        }
        return region;
    }
    
    static fromJSONArray(array: Array<any>): Region[] {
        return array.map(o => this.fromJSONObject(o));
    }
}


