import { VisitType } from './../app/models/visit-type';
import { HttpClient } from '@angular/common/http';
import { SalesPeriod } from './../app/models/sales-period';
import { Injectable } from '@angular/core';
import { Response } from "@angular/http";
import { Region } from '../app/models/region';
import {Observable} from "rxjs/Observable";
import { Globals } from './globals';
@Injectable()
export class CommonService {

    constructor(private http: HttpClient) { }

    fetchVisitTypes(params?: any): Observable<VisitType[]> {
        return this.http.get(Globals.api + 'visitType/apiList', { params: params }).map((res: any[]) => VisitType.fromJSONArray(res));
    }

    fetchSalesPeriods(params?: any): Observable<SalesPeriod[]> {
        return this.http.get(Globals.api + 'salesPeriod/apiList', { params: params }).map((res: any[]) => SalesPeriod.fromJSONArray(res));
    }

    fetchRegions(params?: any): Observable<Region[]> {
        return this.http.get(Globals.api + 'region/apiList', { params: params }).map((res: any[]) => Region.fromJSONArray(res));
    }
}


