import { AuthService } from './../../providers/auth.service';
import {Component} from '@angular/core';
import {NavController, NavParams} from 'ionic-angular';
import {StoreDetailsActionsPage} from "../store-details-actions/store-details-actions";
import {StoreDetailsOrdersPage} from "../store-details-orders/store-details-orders";
import {StoreDetailsHistoryPage} from "../store-details-history/store-details-history";
import {Storage} from "@ionic/storage";
import {StoreDetailsContactsPage} from "../store-details-contacts/store-details-contacts";
import * as Constants from '../../app/constants';
import {DistributionArticlesPage} from "../distribution-articles/distribution-articles";

@Component({
    selector: 'page-store-details',
    templateUrl: 'store-details.html',
})
export class StoreDetailsPage {
    public ACCESS_CALENDAR = Constants.ACCESS_CALENDAR;
    public ACCESS_STORE_LIST = Constants.ACCESS_STORE_LIST;
    public ACCESS_CONTACTS = Constants.ACCESS_CONTACTS;
    public ACCESS_ORDERS = Constants.ACCESS_ORDERS;
    public ACCESS_ORDERS_HISTORY = Constants.ACCESS_ORDERS_HISTORY;
    public accesses = {};

    public actionsPage = StoreDetailsActionsPage;
    public ordersPage = StoreDetailsOrdersPage;
    public historyPage = StoreDetailsHistoryPage;
    public contactsPage = StoreDetailsContactsPage;
    public articlesPage = DistributionArticlesPage;
    public store;
    public hide = false;


    constructor(public navCtrl: NavController,
                private storage: Storage,
                private authService: AuthService,
                public navParams: NavParams) {
        this.accesses = authService.getAccesses();
        this.accesses[6] = true;

        this.store = this.navParams.data.store;
    }
}



