    public distributionFilter() {
        let modal = this.modalCtrl.create(DistributionFilter_2Page, {
            groups: this.groups_store,
            listings: this.listings_store
        });
        modal.onDidDismiss(data => {

            if (data.articleFilter == 1) {
                for(let art_id in this.records_more.records) {
                    if (this.records_more.records[art_id] > 0) {
                        this.records_more.noArticle[art_id] = true;
                    }
                }
            }
            if (data.articleFilter == 0) {
                for(let art_id in this.records_more.records) {
                    this.records_more.noArticle[art_id] = false;
                }
            }

            if (data.listingFilter == 'all') {
                for (let group_index in this.groups) {
                    for (let article_index in this.groups[group_index].articles) {
                        let listing_checker = {
                            checker: false,
                            listings: []
                        };
                        for (let listing_filter in this.groups[group_index].articles[article_index].article.listings.listings) {
                            if (this.groups[group_index].articles[article_index].article.listings.listings[listing_filter].storeCategoryId == this.filterlistings.storeCategoryId) {
                                listing_checker.listings.push(this.groups[group_index].articles[article_index].article.listings.listings[listing_filter]);
                            }
                        }
                        if (listing_checker.listings.length > 0) {
                            listing_checker.checker = true;
                        }
                        this.groups[group_index].articles[article_index].article.listings = listing_checker;
                    }
                }

                for (let bla5 in this.articles) {
                    let blabla = {
                        checker: true,
                        listings: []
                    };
                    for (let blasdf in this.articles[bla5].article.listings.listings) {
                        if (this.articles[bla5].article.listings.listings[blasdf].storeCategoryId == this.filterlistings.storeCategoryId) {
                            blabla.listings.push(this.articles[bla5].article.listings.listings[blasdf]);
                        }
                    }
                    this.articles[bla5].article.listings = blabla;
                }
                console.log(this.articles);
            }
            if (data.listingFilter == 0) {
                for (let bla5 in this.groups) {
                    for (let bla123 in this.groups[bla5].articles) {
                        let blabla = {
                            checker: true,
                            listings: []
                        };
                        for (let blasdf in this.groups[bla5].articles[bla123].article.listings.listings) {
                            if (this.groups[bla5].articles[bla123].article.listings.listings[blasdf].storeCategoryId == this.filterlistings.storeCategoryId) {

                                blabla.listings.push(this.groups[bla5].articles[bla123].article.listings.listings[blasdf]);
                            }
                        }
                        this.groups[bla5].articles[bla123].article.listings = blabla;
                    }
                }
            }
        });
        modal.present();
    }
