import { HttpClient, HttpParams } from '@angular/common/http';
import {Component} from '@angular/core';
import {
    ActionSheetController, AlertController, App, ItemSliding, LoadingController, ModalController, NavController,
    NavParams
} from 'ionic-angular';
import {Storage} from '@ionic/storage';
import {ContactProvider} from "../../providers/contact/contact";
import {OrderCartPage} from "../order-cart/order-cart";
import {StoreDetailsContactAddPage} from "../store-details-contact-add/store-details-contact-add";
import {StoreDetailsContactDetailPage} from "../store-details-contact-detail/store-details-contact-detail";
import {CallNumber} from "@ionic-native/call-number";
import { Globals } from '../../providers/globals';

@Component({
    selector: 'page-store-details-contacts',
    templateUrl: 'store-details-contacts.html',
})

export class StoreDetailsContactsPage {
    public store: any;
    public user: any;
    public contacts: any;

    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                public loader: LoadingController,
                private app: App,
                public http: HttpClient,
                private _contactProvider: ContactProvider,
                public nav: NavController,
                private modalCtrl: ModalController,
                private actionSheetCtrl: ActionSheetController,
                private callNumber: CallNumber,
                private alertCtrl: AlertController,
                storage: Storage) {
        this.store = this.navParams.data;
        storage.get('user').then((user) => {
            this.user = user;
            this.downloadContacts();
        });
    }

    public downloadContacts() {
        let contacts_loader = this.loader.create({
            content: 'Loading Contacts'
        });
        contacts_loader.present().then(() => {
            let params = new HttpParams().append('id', this.store.id)
            .append('expand', 'storeTags.tag,storeContacts,category,customFieldDatas');
            this.http.get(Globals.api + 'app/store/view', {params}).subscribe((store: any) => {
                this.contacts = store.storeContacts;
                contacts_loader.dismissAll();
            })
        });
    }

    public showContact(contact) {
        let modal = this.modalCtrl.create(StoreDetailsContactDetailPage, {
            contact: contact
        });
        modal.present();
    }

    public back() {
        this.navCtrl.pop();
        // this.app.navPop();
    }

    public deleteContact(contact, item_index, item: ItemSliding) {
        item.close();

        let loader = this.loader.create({
            content: 'Deleting contact'
        });
        loader.present().then(() => {
            let params = {
                id: contact.id
            };
            this._contactProvider.deleteContact(params)
                .subscribe(item => {
                    console.log(item);
                    this.contacts.splice(item_index, 1);
                    loader.dismiss();
                })
        });
    }

    public addContact() {
        let modal = this.modalCtrl.create(StoreDetailsContactAddPage, {
            store: this.store,
            isEdit: 0
        });
        modal.onDidDismiss(data => {
            this.downloadContacts();
        });
        modal.present();
    }

    public editContact(contact) {
        let modal = this.modalCtrl.create(StoreDetailsContactAddPage, {
            store: this.store,
            contact: contact,
            isEdit: 1
        });
        modal.onDidDismiss(data => {
            this.downloadContacts();
        });
        modal.present();
    }

    public contactActions(contact) {
        let actionSheet = this.actionSheetCtrl.create({
            title: 'Contact Actions',
            buttons: [
                {
                    text: 'Details',
                    handler: () => {
                        this.showContact(contact);
                    }
                },
                {
                    text: 'Call',
                    handler: () => {
                        this.callContact(contact);
                    }
                },
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: () => {
                        console.log('Cancel clicked');
                    }
                }
            ]
        });
        actionSheet.present();
    }

    public callContact(contact) {
        if (contact.mobile) {
            this.callNumber.callNumber(contact.mobile, true)
                .then(() => console.log('Launched dialer!'))
                .catch(() => console.log('Error launching dialer'));
        } else {
            let alert = this.alertCtrl.create({
                title: 'No Phone #',
                subTitle: 'The contact doesn\'t have phone #. Please add phone #',
                buttons: ['Ok']
            });
            alert.present();
        }

    }
}



