import { VisitService } from './../../providers/visit.service';
import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, AlertController } from 'ionic-angular';
import { Visit } from '../../app/models/visit';
import { VisitType } from '../../app/models/visit-type';
import { StoreDetailsPage } from '../store-details/store-details';

/**
 * Generated class for the VisitEditModalPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-visit-edit-modal',
  templateUrl: 'visit-edit-modal.html',
})
export class VisitEditModalPage {
  public visit: Visit;
  public visitTypes: VisitType[] = [];
  private store: any;
  public dateTimePickerOptions: any = {
    timeFormat: 'H:ii',
    dateFormat: 'd MM yy', 
    dateWheels: '|D M d|'
  };
  constructor(
    public viewCtrl: ViewController, 
    public navCtrl: NavController,
    public navParams: NavParams,
    public alertCtrl: AlertController
  ) {
    this.visit = navParams.data.visit;
    //this.visit.start = this.visit.startDate.toISOString();
    //this.visit.end = this.visit.endDate.toISOString();
    this.visitTypes = navParams.data.visitTypes;
    this.store = navParams.data.store;
    console.log(this.store);
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

  public goToStoreDetails() {
    this.navCtrl.push(StoreDetailsPage, { store: this.store });
  }

  save() {
    this.viewCtrl.dismiss({reason: 'update', editedVisit: this.visit});
  }

  delete() {
    let prompt = this.alertCtrl.create({
      title: 'Confirm',
      message: "Are you sure you want to delete this visit?",
      buttons: [
        {
          text: 'Cancel'
        },
        {
          text: 'Yes',
          handler: data => {
            this.viewCtrl.dismiss({reason: 'delete'})
          }
        }
      ]
    });
    prompt.present();
  }
}



