import { Component } from '@angular/core';
import {NavController, NavParams, ViewController} from 'ionic-angular';

/**
 * Generated class for the DistributionNotePage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-distribution-note',
  templateUrl: 'distribution-note.html',
})
export class DistributionNotePage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController) {
  }

  public note:any;

  ionViewDidLoad() {
    console.log('ionViewDidLoad DistributionNotePage');
  }

  public dismiss() {
    this.viewCtrl.dismiss({note: this.note});
  }

  public saveNote() {
    this.viewCtrl.dismiss({note: this.note})
  }



}



