import { Pipe, PipeTransform } from "@angular/core";
import * as moment from 'moment';
import * as numeral from 'numeral';

@Pipe({
    name: 'storeFilter',
    pure: false
})
export class StoreFilterPipe implements PipeTransform {
    transform(items: any[], filter: string, all_stores: any[]): any {

        if (!items || !filter) {
            return items;
        }
        // filter items array, items which match and return true will be kept, false will be filtered out
        return all_stores.filter(item => item.customer.toLowerCase().indexOf(filter.toLowerCase()) > -1).slice(0, 20);
    }
}

@Pipe({
    name: 'numerally',
    pure: false
})
export class NumerallyPipe implements PipeTransform {
    transform(numberValue: number, format: string): string {
        if(numberValue == Infinity || numberValue == -Infinity || isNaN(numberValue) || numberValue == null) {
            numberValue = 0;
        }
        return numeral(numberValue).format(format);
    }
}


@Pipe({
    name: 'filterFn',
    pure: false
})
export class FilterFnPipe implements PipeTransform {
    transform(array: any[], fn): any[] {
        return array.filter(fn);
    }
}



