import * as moment from 'moment';

export class SalesPeriod {
    public id: number;
    public name: string;
    public startDate: Date;
    public endDate: Date;

    constructor(id: number, name: string, startDate: string, endDate: string) {
        this.id = id;
        this.name = name;
        this.startDate = moment(startDate).toDate();
        this.endDate = moment(endDate).toDate();
    }

    static fromJSONArray(array: Array<any>): SalesPeriod[] {
        return array.map(o => this.fromJSONObject(o));
    }

    static fromJSONObject(o: any): SalesPeriod {
        return new SalesPeriod(o.id, o.name, o.start_date, o.end_date);
    }
}


