import { PlanVisitPage } from './../plan-visit/plan-visit';
import { DashboardService } from './../../providers/dashboard.service';
import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController } from 'ionic-angular';
import { CommonService } from '../../providers/common.service';
import _ from 'lodash';

/**
 * Generated class for the DistributionThirdPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-distribution-third',
  templateUrl: 'distribution-third.html',
})
export class DistributionThirdPage {

  activeSegment = 'missing';
  distribution: any;
  categories: any;
  
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    private commonService: CommonService,
    public dashboardService: DashboardService,
    private loader: LoadingController) {
    let loading = this.loader.create({
      content: 'Loading report'
    });
    console.log(navParams.data.article);
    loading.present().then(() => {
      dashboardService.fetchDistributionReport(navParams.data.id).subscribe(distribution => {
        this.distribution = distribution;
        this.categories = this.prepareData(navParams.data.article.id, navParams.data.sellerId);

        loading.dismissAll();
      });
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DistributionThirdPage');
  }

  prepareData(articleId, sellerId): any {
    let categories = _.cloneDeep(this.distribution.storeCategories);
    let currentSeller = this.distribution.sellers.find(seller => seller.id == sellerId);
    categories.forEach(category => {
      var stores = currentSeller.stores.filter(store => store.categoryId == category.id);
      stores.forEach(store => {
        let article = store.articles.find(article => article.id == articleId);
        store.distributionValue = article ? article.quantity : null;
      });
      category.stores = stores;
    });
    return categories;
  }

  missing(store){
    return !store.distributionValue;
  }

  planVisit(store) {
    this.navCtrl.push(PlanVisitPage, {store: store});
  }
}



