export class VisitType {
    public id: number;
    public name: string;
    public color: string;
    constructor(id: number, name: string, color: string) {
        this.id = id;
        this.name = name;
        this.color = color;
    }
    static fromJSONObject(o: any): VisitType {
        return new VisitType(o.id, o.name, o.color);
    }

    static fromJSONArray(array: Array<any>): VisitType[] {
        return array.map(o => this.fromJSONObject(o));
    }
}


